const API_BASE_URL = 'http://localhost:3001/api';

export async function fetchClients() {
  const res = await fetch(`${API_BASE_URL}/clients`);
  if (!res.ok) throw new Error('Failed to fetch clients');
  return res.json();
}

export async function fetchOperators() {
  const res = await fetch(`${API_BASE_URL}/operators`);
  if (!res.ok) throw new Error('Failed to fetch operators');
  return res.json();
}

export async function fetchCompanies(params?: { status?: string; rank?: string; search?: string }) {
  const query = new URLSearchParams(params as Record<string, string>).toString();
  const res = await fetch(`${API_BASE_URL}/companies?${query}`);
  if (!res.ok) throw new Error('Failed to fetch companies');
  return res.json();
}

export async function fetchCallLogs() {
  const res = await fetch(`${API_BASE_URL}/call-logs`);
  if (!res.ok) throw new Error('Failed to fetch call logs');
  return res.json();
}

export async function createCallLog(data: any) {
  const res = await fetch(`${API_BASE_URL}/call-logs`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data),
  });
  if (!res.ok) throw new Error('Failed to create call log');
  return res.json();
}